import uniSearch from '../../components/lee-search/lee-search.vue'
	export default {
 
		data() {
			return {
				titleNViewBackground: '',
				swiperCurrent: 0,
				swiperLength: 0,
				carouselList: [],
				goodsList: [],
				date: new Date(+new Date(new Date().toJSON())+8*3600*1000).toISOString(),
				
					iconSrc: {
						logo: '../../static/lee-search/icon_search.png',
						voice: '../../static/lee-search/icon_voice.png',
						scan: '../../static/lee-search/icon_scan.png',
						clear:'../../static/lee-search/icon_clear.png'
					},
					inputAttr: {
						backgroundColor: '#ccc',
						placeholderText: '请输入关键字'
					}
			};
		},
      components:{
     	 uniSearch
      },
		onLoad() {
			this.loadData();
		},
		methods: {
			/**
			 * 请求静态数据只是为了代码不那么乱
			 * 分次请求未作整合
			 */
			search (){
				
			},
			async loadData() {
				let carouselList = await this.$api.json('carouselList');
				this.titleNViewBackground = carouselList[0].background;
				this.swiperLength = carouselList.length;
				this.carouselList = carouselList;
				
				let goodsList = await this.$api.json('goodsList');
				this.goodsList = goodsList || [];
			},
			//轮播图切换修改背景色
			swiperChange(e) {
				const index = e.detail.current;
				this.swiperCurrent = index;
				this.titleNViewBackground = this.carouselList[index].background;
			},
			//详情页
			navToDetailPage(item) {
				//测试数据没有写id，用title代替
				let id = item.title;
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			},
		},
		navToCategory(id) {
				//测试数据没有写id，用title代替
				uni.navigateTo({
					url: `/pages/category/category?pid=${id}`
				})
			},

























	}
